.b-block-grid {
  background-color: siteColor(vam-white);
  margin: 0 0 $spacing-small;


  &--padded {
    margin: 20px 0 0;
    overflow: hidden;
    padding: 0 0 (48px - 10px);

    @include bpMinSmall {
      padding: 0 (32px - 10px) (48px - 10px);
    }

    @include bpMinMedium {
      padding: 0 (64px - 10px) (64px - 10px);
    }
  }

  &__header {
    background-color: siteColor(vam-white);
    color: siteColor(vam-black);
    padding: $spacing-medium $spacing-small;
    width: 100%;

    @include bpMinSmall {
      padding: 54px $spacing-small;
    }

    &--transparent {
      background-color: transparent;
      color: siteColor(vam-white);
    }
  }

  &__title {
    @include typeSetting(5, bold);
    text-align: center;

    @include bpMinSmall {
      @include typeSetting(6, bold);
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
  }

  &__footer {
    a:focus {
      box-shadow: none;
      outline: 0;
      .b-icon-badge__icon {
        @include defaultFocus;
      }
    }

    background-color: siteColor(vam-black);
    clear: both;
    padding: $spacing-small * 2;

    &--light {
      background-color: siteColor(vam-white);
    }

    svg {
      color: siteColor(vam-black);
    }
  }

  &__cols-restarter {
    // sass-lint:disable no-important
    column-span: all !important;
    height: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
    // sass-lint:enable no-important
  }
}
