.b-icon-link {
  color: siteColor(vam-black);
  cursor: pointer;
  display: inline-block;
  height: 45px;
  position: relative;
  width: 45px;

  &--light {
    color: siteColor(vam-white);
  }

  &__download {
    border: 1px solid siteColor(vam-black);
    border-radius: 50%;
    cursor: default;
  }

  &--active {
    background-color: siteColor(secondary-green);
    border-color: siteColor(secondary-green);
    border-radius: 50%;
    cursor: pointer;
    opacity: 1;
  }

  &__container {
    &::after {
      border: 1px solid currentColor;
      border-radius: 50%;
      content: '';
      display: inline-block;
      height: 100%;
      opacity: .25;
      position: absolute;
      transition: opacity .2s;
      width: 100%;

      .b-icon-link:hover & {
        opacity: 1;
      }

      .b-icon-link--no-border & {
        border-width: 0;
      }

      .b-icon-link--no-border:hover & {
        @include bpMaxXSmall {
          background-color: transparent;
          border-width: 0;
        }

        background-color: siteColor(vam-black);
        border: 1px solid siteColor(vam-black);
        opacity: .6;
        z-index: 0;
      }
    }
    &-download {
      &::after {
        content: none;
        cursor: pointer;
        opacity: 1;
      }
    }
  }

  &__icon {
    bottom: 27%;
    height: 46%;
    left: 27%;
    position: absolute;
    right: 27%;
    top: 27%;
    width: 46%;
    z-index: 1;
  }
}
